.tb_video_ico, .tb_audio_ico, .tb_shop_ico, .tb_play_ico, .tb_reel_ico {
    position: absolute;
    right: var(--tb-icon-position);
    top: var(--tb-icon-position);
    z-index: 2;
    color: #fff;
    //font-size: var(--tb-icon-position);
}

.tb__media_ico_ .tb__icon{
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.2));
    transition: all .225s .01s ease-out;
}

.tb_video_ico{
    font-size: 13px;
}

.tb_reel_ico{
    font-size: 18px;
}

.tb_audio_ico, .tb_shop_ico{
    font-size: 16px;
}
.tb_play_ico{
    font-size: 20px;
}

.tb_multiple_ico{
    position: absolute;
    color: #fff;
    font-size: 16px;
    z-index: 2;
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.2));
}

.tb__media_ico_c .tb_youtube_ico, 
.tb__media_ico_c .tb_center_video_ico {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    text-align: center;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: calc(38px + .5vw);
    z-index: 2;
}

.tb_center_video_ico{
    color: #fff;
    opacity: .8;
}

.tb__media_ico_ .tb__icon:nth-child(1){
    left: auto;
    right: var(--tb-icon-position);
    top: var(--tb-icon-position);
}

.tb__media_ico_ .tb__icon:nth-child(2){
    left: var(--tb-icon-position);
    right: auto;
    top: var(--tb-icon-position);
}

.tb__media_ico_ .tb__icon:nth-child(3){
    left: var(--tb-icon-position);
    right: auto;
    top: auto;
    bottom:var(--tb-icon-position);
}

.tb_icon_animate:hover .tb_video_ico,
.tb_icon_animate:hover .tb_shop_ico,
.tb_icon_animate:hover .tb_audio_ico,
.tb_icon_animate:hover .tb_reel_ico{
    opacity: 0;
    transform: scaleX(.4) scaleY(.4) translateZ(0);
}